<template>
  <div class="list-card-container">
    <header class="flex">
      <p class="header-title">{{ title }}</p>
      <el-popover
        v-if="checkedList.length > 0"
        placement="right"
        width="400"
        trigger="click"
      >
        <el-checkbox
          v-for="(item, index) in checkedList"
          :key="index"
          v-model="item.checked"
          @change="handleChangeCheck($event, item)"
        >
          {{ item.label }}
        </el-checkbox>

        <el-image
          slot="reference"
          class="header-icon-style"
          :src="require('@/assets/icon/more_icon.png')"
          fit="fill"
        ></el-image>
      </el-popover>

      <el-popover
        v-if="radioList.length > 0"
        placement="right"
        width="400"
        trigger="click"
      >
        <el-radio-group
          @change="$emit('changeRadio', radioValue)"
          v-model="radioValue"
        >
          <el-radio
            :label="item.value"
            v-for="(item, index) in radioList"
            :key="index"
            >{{ item.label }}</el-radio
          >
        </el-radio-group>
        <el-image
          slot="reference"
          class="header-icon-style"
          :src="require('@/assets/icon/more_icon.png')"
          fit="fill"
        ></el-image>
      </el-popover>
    </header>

    <main>
      <slot></slot>
    </main>
  </div>
</template>

<script>
export default {
  props: {
    // 标题文字
    title: {
      type: String,
      default: "标题",
    },
    // 是否自动滚动
    autoScroll: {
      type: Boolean,
      default: false,
    },
    // 多选参数
    checkedList: {
      type: [Array, Object],
      default: () => [],
    },
    // radio参数
    radioList: {
      type: [Array, Object],
      default: () => [],
    },
  },
  data() {
    return {
      radioValue: "city",
      timer: null,
    };
  },
  updated() {
    if (this.autoScroll) {
      this.handleScroll();
    }
  },
  beforeDestroy() {
    console.log("beforeDestory:>>");
    clearInterval(this.timer);
  },
  methods: {
    handleScroll() {
      const el = this.$refs.scroll.$children[1].wrap;
      console.log("el :>> ", el);
      window.ele = el;
      el.scrollTop = 0;
      this.timer = setInterval(() => {
        el.scrollTop = el.scrollTop + 1;
        console.log(" el.scrollTop :>> ", el.scrollTop);
      }, 300);
    },

    handleChangeCheck(e, row) {
      console.log("e :>> ", e);
      console.log("row :>> ", row);
      this.$emit("changeCheck", e, row);
    },
  },
};
</script>

<style lang="scss" scoped>
.list-card-container {
  padding: px(24) px(22) px(29) px(35);
  width: px(399 - 22 - 35);
  height: px(233 - 24 - 29);
  background: border-box no-repeat center / 100% 100%
    url("~@/assets/images/bottom_frame_big.png");
  color: #fff;

  header {
    margin-bottom: px(17);
    .header-title {
      font-size: px(18);
      font-family: Source Han Sans SC;
      font-weight: bold;
      color: #70e1f8;
    }
    .header-icon-style {
      width: px(20);
      height: px(18);
    }
  }
  main {
    width: px(318);
    height: px(146);
  }
}
</style>
